<script>
   export default {
      name: 'HelloWorld',
      props: {
         msg: String
      },
      mounted() {
         this.drawLine();
         this.drawLine2();
      },
      methods: {
         drawLine2() {
            let circularChart = this.$echarts.init(document.getElementById('circularChart'));
            circularChart.setOption({
               tooltip: {
                  trigger: 'item',
                  formatter: "{a} <br/>{b}: {c} ({d}%)"
               },
               /* legend: {
                    orient: 'vertical',
                    x: 'left',
                    data:['直接访问','邮件营销','联盟广告','视频广告','搜索引擎']
                }, */
               series: [{
                  name: '访问来源',
                  type: 'pie',
                  radius: ['50%', '70%'],
                  avoidLabelOverlap: false,
                  label: {
                     normal: {
                        show: false,
                        position: 'center'
                     },
                     emphasis: {
                        show: true,
                        textStyle: {
                           fontSize: '30',
                           fontWeight: 'bold'
                        }
                     }
                  },
                  labelLine: {
                     normal: {
                        show: false
                     }
                  },
                  data: [{
                        value: 335,
                        name: '直接访问'
                     },
                     {
                        value: 310,
                        name: '邮件营销'
                     },
                     {
                        value: 234,
                        name: '联盟广告'
                     },
                     {
                        value: 135,
                        name: '视频广告'
                     },
                     {
                        value: 1548,
                        name: '搜索引擎'
                     }
                  ]
               }]
            });
         },
         drawLine() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart'));

            // 绘制图表
            myChart.setOption({
               /* legend:{
                  type:'plain',
                  top:0
               },
               xAxis: {
                  type: 'category',
                  data: ['$10','$20','$30','$40','$50']
               },
               yAxis: {
                  type: 'category',
                  data: ['0','25%','50%','100%']
               },
               series: [{
                  data: [],
                  type: 'line',
                  smooth: true
               }] */
               grid: {
                  top: 5,
                  left: 50
               },
               tooltip: {
                  trigger: 'axis'
               },
               xAxis: [{
                  type: 'category',
                  data: ['$10', '$20', '$30', '$40', '$50'],
                  axisLine: {
                     lineStyle: {
                        color: "#999"
                     }
                  }
               }],
               yAxis: [{
                  type: 'value',
                  splitNumber: 4,
                  offset: 0,
                  axisLabel: {
                     show: true,
                     interval: 'auto',
                     formatter: '{value} %'
                  },
                  splitLine: {
                     lineStyle: {
                        type: 'dashed',
                        color: '#DDD'
                     }
                  },
                  axisLine: {
                     show: false,
                     lineStyle: {
                        color: "#333"
                     },
                  },
                  nameTextStyle: {
                     color: "#999"
                  },
                  splitArea: {
                     show: false
                  }
               }],
               series: [{
                  /* name: '课时', */
                  type: 'line',
                  data: [23, 60, 20, 36, 23, 85],
                  lineStyle: {
                     normal: {
                        width: 8,
                        color: {
                           type: 'linear',

                           colorStops: [{
                              offset: 0,
                              color: '#A9F387' // 0% 处的颜色
                           }, {
                              offset: 1,
                              color: '#48D8BF' // 100% 处的颜色
                           }],
                           globalCoord: false // 缺省为 false
                        },
                        shadowColor: 'rgba(72,216,191, 0.3)',
                        shadowBlur: 10,
                        shadowOffsetY: 20
                     }
                  },
                  itemStyle: {
                     normal: {
                        color: '#fff',
                        borderWidth: 10,
                        /*shadowColor: 'rgba(72,216,191, 0.3)',
                        shadowBlur: 100,*/
                        borderColor: "#A9F387"
                     }
                  },
                  smooth: true
               }]
            });
         }
      }
   }
</script>

